import Vue from 'vue'
import Router from 'vue-router'
import fileList from '@/components/fileList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'fileList',
      component: fileList
    }
  ],
  mode: 'history'
})



